import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/logo.png';
import { useEffect } from 'react';

function Header() {
  const changeState = useDispatch();

  // header.
  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginHospital = useSelector((state) => state.hospital);
  const loginCti = useSelector((state) => state.cti);
  const ctiselector = useSelector((state) => state.ctiselector);
  const gestorselector = useSelector((state) => state.gestorselector);
  const gestor = useSelector((state) => state.gestor);
  const corrida = useSelector((state) => state.corrida);
  const password = useSelector((state) => state.password);
  
  // função para retornar ao componente 'Login'.
  const clickLogin = () => {
    changeState({ type: 'LOGIN_ON' });
  };

  // função para retornar ao componente 'Lista'.
  const clickVoltar = () => {
    if (ctiselector === 1 || gestorselector === 1) {
      changeState({
        type: 'LOGIN_ON',
      });
    } else if ((corrida === 1 || password === 1) && gestor === 1) {
      changeState({
        type: 'GESTOR_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
      });
    } else if ((corrida === 1 || password === 1) && gestor === 0) {
      changeState({
        type: 'CTI_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
      });
    }
  };

  // função para acessar o componente 'Password'.
  const clickPassword = () => {
    changeState({
      type: 'PASSWORD_ON',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
      payloadGestor: gestor,
    });
  };

  function Title() {
    if (gestor === 1) {
      return (
        <p className="title1" style={{ color: '#1F7A8C' }}>
          {'PAINEL DO GESTOR'}
        </p>
      );
    } else {
      return (
        <p className="title1" style={{ color: '#1F7A8C' }}>
          {loginHospital + ' - ' + loginCti}
        </p>
      );
    }
  }

  // renderização do componente.
  return (
    <div
      className="fade-in"
      style={{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        margin: 0,
        padding: 0,
        width: '100%',
        maxHeight: '20vh',
      }}
    >
      <div
        className="secondary"
        style={{
          backgroundColor: '#E1E5F2',
          borderRadius: 5,
          height: 115,
          marginRight: 0,
        }}
      >
        <img
          alt=""
          src={logo}
          style={{
            marginTop: 15,
            marginBottom: 15,
            marginLeft: -5,
            marginRight: -5,
            height: '90%',
          }}
        ></img>
      </div>
      <div
        className="secondary"
        style={{
          backgroundColor: '#E1E5F2',
          width: '100%',
          height: 115,
        }}
      >
        <Title></Title>
      </div>
      <div
        className="secondary"
        style={{
          backgroundColor: '#E1E5F2',
          marginLeft: 0,
          height: 115,
        }}
      >
        <button
          className="blue-button"
          title="CLIQUE PARA ALTERAR SUA SENHA."
          onClick={() => clickPassword()}
          style={{
            width: '95%',
            marginBottom: 0,
            paddingLeft: 20,
            paddingRight: 20,
          }}
        >
          OLÁ, {loginUsuario}.
        </button>
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <button
            className="blue-button"
            title="RETORNAR À LISTA DE PACIENTES."
            style={{ width: 100 }}
            onClick={() => clickVoltar()}
          >
            VOLTAR
          </button>
          <button
            className="blue-button"
            title="FAZER LOGOFF."
            onClick={() => clickLogin()}
            style={{ marginLeft: 0, width: 100 }}
          >
            SAIR
          </button>
        </div>
      </div>
    </div>
  );
}
export default Header;
