import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useEffect } from 'react';
import axios from 'axios';
import deletar from '../images/deletar.svg';

function Images() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';
  const changeState = useDispatch();

  // header.
  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginHospital = useSelector((state) => state.hospital);
  const loginCti = useSelector((state) => state.cti);
  const gestor = useSelector((state) => state.gestor);

  // dados do paciente.
  const idpct = useSelector((state) => state.idpct);
  var box = useSelector((state) => state.box);
  var entrada = useSelector((state) => state.entrada);
  var nome = useSelector((state) => state.nome);
  var dn = useSelector((state) => state.dn);
  var hd = useSelector((state) => state.hd);
  var ap = useSelector((state) => state.ap);
  var alergias = useSelector((state) => state.alergias);
  var medprev = useSelector((state) => state.medprev);
  var exprev = useSelector((state) => state.exprev);
  var hda = useSelector((state) => state.hda);
  var status = useSelector((state) => state.status);
  var inter = useSelector((state) => state.inter);

  // invasões.
  var va = useSelector((state) => state.va);
  var dataVa = useSelector((state) => state.datava);
  var jid = useSelector((state) => state.jid);
  var dataJid = useSelector((state) => state.datajid);
  var jie = useSelector((state) => state.jie);
  var dataJie = useSelector((state) => state.datajie);
  var subcld = useSelector((state) => state.subcld);
  var dataSubcld = useSelector((state) => state.datasubcld);
  var subcle = useSelector((state) => state.subcle);
  var dataSubcle = useSelector((state) => state.datasubcle);
  var radd = useSelector((state) => state.radd);
  var dataRadd = useSelector((state) => state.dataradd);
  var rade = useSelector((state) => state.rade);
  var dataRade = useSelector((state) => state.datarade);
  var vfemd = useSelector((state) => state.vfemd);
  var dataVfemd = useSelector((state) => state.datavfemd);
  var vfeme = useSelector((state) => state.vfeme);
  var dataVfeme = useSelector((state) => state.datavfeme);
  var afemd = useSelector((state) => state.afemd);
  var dataAfemd = useSelector((state) => state.dataafemd);
  var afeme = useSelector((state) => state.afeme);
  var dataAfeme = useSelector((state) => state.dataafeme);
  var snc = useSelector((state) => state.snc);
  var dataSnc = useSelector((state) => state.datasnc);
  var torax = useSelector((state) => state.torax);
  var dataTorax = useSelector((state) => state.datatorax);
  var abd = useSelector((state) => state.abd);
  var dataAbd = useSelector((state) => state.dataabd);
  var svd = useSelector((state) => state.svd);
  var dataSvd = useSelector((state) => state.datasvd);

  // vm.
  var vm = useSelector((state) => state.vm);
  var pp = useSelector((state) => state.pp);
  var peep = useSelector((state) => state.peep);
  var fi = useSelector((state) => state.fi);

  useEffect(() => {
    setToogle(0);
    list();
    // eslint-disable-next-line
  }, []);

  // função para obtenção das listas de imagens (tipo 9).
  const [rx, setRx] = useState([]);
  const [lesao, setLesao] = useState([]);
  const type = 9;
  const typeRx = 'RX';
  const typeLesao = 'LESAO';
  const list = () => {
    axios
      .get(html + '/rx/' + idpct + '/' + type + '/' + typeRx)
      .then((response) => {
        setRx(response.data);
      });
    axios
      .get(html + '/lesao/' + idpct + '/' + type + '/' + typeLesao)
      .then((response) => {
        setLesao(response.data);
      });
  };

  const [toogle, setToogle] = useState(1); // 0 para exibir rx, 1 para exibir lesões.
  const [colorRx, setColorRx] = useState('menu-button');
  const [colorLesao, setColorLesao] = useState('blue-button');
  const toogleRx = () => {
    list();
    setToogle(0);
    console.log('SELECIONADA ABA RX');
    setColorRx('menu-button');
    setColorLesao('blue-button');
  };
  const toogleLesao = () => {
    list();
    setToogle(1);
    console.log('SELECIONADA ABA LESÃO');
    setColorRx('blue-button');
    setColorLesao('menu-button');
  };

  // funções para exibição as listas de imagens (rx e lesões).
  function ShowRx() {
    return (
      <div
        className="scrollview"
        style={{
          height: 365,
          width: 770,
          flexDirection: 'row',
          flexWrap: 'wrap',
          justifyContent: 'flex-start',
          alignItems: 'flex-start',
          marginTop: 0,
          marginBottom: 0,
        }}
      >
        {rx.map((item) => (
          <p className="rowimage" key={item.id}>
            <button
              className="delete-button"
              title="EXCLUIR RX"
              onClick={() => clickDeleteImagem(item.id)}
              style={{
                position: 'absolute',
                top: 5,
                right: 5,
                height: 30,
                width: 30,
              }}
            >
              <img
                alt=""
                src={deletar}
                style={{
                  marginTop: 20,
                  marginBottom: 20,
                  marginLeft: 20,
                  marginRight: 20,
                  height: 20,
                  width: 20,
                }}
              ></img>
            </button>
            <img
              className="images"
              alt=""
              title={'DATA: ' + item.date1}
              src={item.image}
              onClick={() => openPopImage(item.image)}
            ></img>
          </p>
        ))}
      </div>
    );
  }
  function ShowLesao() {
    return (
      <div
        className="scrollview"
        style={{
          height: 365,
          width: 770,
          flexDirection: 'row',
          flexWrap: 'wrap',
          justifyContent: 'flex-start',
          alignItems: 'flex-start',
          marginTop: 0,
          marginBottom: 0,
        }}
      >
        {lesao.map((item) => (
          <p className="rowimage" key={item.id}>
            <img
              className="images"
              alt=""
              title={'DATA: ' + item.date1}
              src={item.image}
              onClick={() => openPopImage(item.image)}
            ></img>
            <button
              className="red-button"
              title="EXCLUIR IMAGEM"
              onClick={() => clickDeleteImagem(item.id)}
              style={{ height: 200 }}
            >
              x
            </button>
          </p>
        ))}
      </div>
    );
  }

  // função para exclusão da imagem.
  const clickDeleteImagem = (id) => {
    fetch(html + '/deleteimagem/' + id);
    changeState({
      type: 'TOAST_SUCCESS',
      payloadToastColor: '#52BE80',
      payloadToastMessage: 'FOTO EXCLUÍDA COM SUCESSO',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
      payloadGestor: gestor,
      payloadIdpct: idpct,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadDatava: dataVa,
      payloadJid: jid,
      payloadDatajid: dataJid,
      payloadJie: jie,
      payloadDatajie: dataJie,
      payloadSubcld: subcld,
      payloadDatasubcld: dataSubcld,
      payloadSubcle: subcle,
      payloadDatasubcle: dataSubcle,
      payloadRadd: radd,
      payloadDataradd: dataRadd,
      payloadRade: rade,
      payloadDatarade: dataRade,
      payloadVfemd: vfemd,
      payloadDatavfemd: dataVfemd,
      payloadVfeme: vfeme,
      payloadDatavfeme: dataVfeme,
      payloadAfemd: afemd,
      payloadDataafemd: dataAfemd,
      payloadAfeme: afeme,
      payloadDataafeme: dataAfeme,
      payloadSvd: svd,
      payloadDatasvd: dataSvd,
      payloadSnc: snc,
      payloadDatasnc: dataSnc,
      payloadTorax: torax,
      payloadDatatorax: dataTorax,
      payloadAbd: abd,
      payloadDataabd: dataAbd,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
    setTimeout(() => {
      changeState({
        type: 'CORRIDA_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
        payloadIdpct: idpct,
        payloadBox: box,
        payloadEntrada: entrada,
        payloadNome: nome,
        payloadDn: dn,
        payloadHd: hd,
        payloadAp: ap,
        payloadAlergias: alergias,
        payloadMedprev: medprev,
        payloadExprev: exprev,
        payloadHda: hda,
        payloadStatus: status,
        payloadVa: va,
        payloadDatava: dataVa,
        payloadJid: jid,
        payloadDatajid: dataJid,
        payloadJie: jie,
        payloadDatajie: dataJie,
        payloadSubcld: subcld,
        payloadDatasubcld: dataSubcld,
        payloadSubcle: subcle,
        payloadDatasubcle: dataSubcle,
        payloadRadd: radd,
        payloadDataradd: dataRadd,
        payloadRade: rade,
        payloadDatarade: dataRade,
        payloadVfemd: vfemd,
        payloadDatavfemd: dataVfemd,
        payloadVfeme: vfeme,
        payloadDatavfeme: dataVfeme,
        payloadAfemd: afemd,
        payloadDataafemd: dataAfemd,
        payloadAfeme: afeme,
        payloadDataafeme: dataAfeme,
        payloadSvd: svd,
        payloadDatasvd: dataSvd,
        payloadSnc: snc,
        payloadDatasnc: dataSnc,
        payloadTorax: torax,
        payloadDatatorax: dataTorax,
        payloadAbd: abd,
        payloadDataabd: dataAbd,
        payloadVm: vm,
        payloadPp: pp,
        payloadPeep: peep,
        payloadFi: fi,
        payloadInter: inter,
      });
      setToogle(0);
    }, 3500);
  };

  // renderizando as listas de imagens.
  function ShowImages() {
    if (toogle === 0) {
      return <ShowRx></ShowRx>;
    } else {
      return <ShowLesao></ShowLesao>;
    }
  }

  // expandindo a imagem.
  const [keyPop, setKeyPop] = useState(0);
  const [image, setImage] = useState('');
  const openPopImage = (image) => {
    setKeyPop(1);
    setImage(image);
  };
  const closePopImage = () => {
    setKeyPop(0);
  };
  function PopImage() {
    if (keyPop === 1) {
      return (
        <div
          className="secondary fade-in"
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            height: '100%',
            width: '100%',
            backgroundColor: 'rgb(0, 0, 0, 0.6)',
            zIndex: 3,
            marginTop: 0,
            marginBottom: 0,
            marginLeft: 0,
            marginRight: 0,
            paddingLeft: 0,
            paddingRight: 0,
            paddingTop: 0,
            paddingBottom: 0,
            borderRadius: 0,
          }}
        >
          <img
            className="images"
            style={{ height: 400 }}
            alt=""
            src={image}
          ></img>
          <button
            className="green-button"
            title="FECHAR IMAGEM"
            onClick={() => closePopImage()}
            style={{ height: 50, width: 50 }}
          >
            x
          </button>
        </div>
      );
    } else {
      return null;
    }
  }

  // renderização do componente.
  return (
    <div className="secondary" style={{ marginTop: 0, marginBottom: 0 }}>
      <div className="secondary" style={{ flexDirection: 'row', marginTop: 0 }}>
        <button
          className={colorRx}
          style={{ width: 100, marginTop: 0, marginBottom: 0 }}
          onClick={() => toogleRx()}
        >
          RX
        </button>
        <button
          className={colorLesao}
          style={{ width: 100, marginTop: 0, marginBottom: 0 }}
          onClick={() => toogleLesao()}
        >
          LESÃO
        </button>
      </div>
      <ShowImages></ShowImages>
      <PopImage></PopImage>
    </div>
  );
}
export default Images;
