import React from 'react';
import { useSelector } from 'react-redux';

function ToastSuccess() {
  const viewToast = useSelector((state) => state.toast);
  const toastColor = useSelector((state) => state.toastColor);
  const toastMessage = useSelector((state) => state.toastMessage);

  if (viewToast === 1) {
    return (
      <div
        className="secondary toast"
        style={{
          position: 'absolute',
          height: '100%',
          width: '100%',
          backgroundColor: 'rgb(0, 0, 0, 0.6)',
          zIndex: 4,
          marginTop: 0,
          marginBottom: 0,
          marginLeft: 0,
          marginRight: 0,
          paddingLeft: 0,
          paddingRight: 0,
          paddingTop: 0,
          paddingBottom: 0,
          borderRadius: 0,
        }}
      >
        <div
          style={{
            right: 15,
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            backgroundColor: toastColor,
            borderRadius: 5,
            marginTop: 5,
            marginBottom: 5,
            marginLeft: 5,
            marginRight: 5,
          }}
        >
          <label
            style={{
              marginTop: 30,
              marginBottom: 30,
              marginLeft: 30,
              marginRight: 30,
              color: '#FFFFFF',
              fontSize: 14,
              fontWeight: 'bold',
              textAlign: 'center',
            }}
          >
            {toastMessage}
          </label>
        </div>
      </div>
    );
  } else {
    return null;
  }
}

export default ToastSuccess;
