  const listControle = () => {
    var type = 3; // tipo de dado inserido na politable (3 = controle).
    // controles.
    axios.get(html + '/controles/' + idpct + '/' + type).then((response) => {
      setControle(response.data);
    });
    // último controle clínico.
    axios.get(html + '/lastcontrole/' + idpct + '/' + type).then((response) => {
      //const x = []; // se o tipo da variável x não for definido como array [], a função .map abaixo não funciona.
      const x = response.data;
      if (x.length > 0) {
        setGlasgow(x.map((controle) => controle.glasgow));
        setPas(x.map((controle) => controle.pas));
        setPad(x.map((controle) => controle.pad));
        var pas = parseInt(x.map((controle) => controle.pas));
        var pad = x.map((controle) => controle.pad);
        setPam(((pas + 2 * pad) / 3).toFixed(0));
        setFc(x.map((controle) => controle.fc));
        setFr(x.map((controle) => controle.fr));
        setSao2(x.map((controle) => controle.sao2));
        setTax(x.map((controle) => controle.tax));
        setBh(x.map((controle) => controle.bh));
        setDiu(x.map((controle) => controle.diu));
        // VERIFICANDO CRITÉRIOS DE SEPSE.
        var item1 = 0; // tax.
        var item2 = 0; // fc.
        var item3 = 0; // fr.
        // SIRS (peso x1).
        if (
          x.map((controle) => controle.tax) < 35 ||
          x.map((controle) => controle.tax) > 37.8
        ) {
          item1 = 1;
        } else {
          item1 = 0;
        }
        if (x.map((controle) => controle.fc) > 90) {
          item2 = 1;
        } else {
          item2 = 0;
        }
        if (
          x.map((controle) => controle.fr) > 20 &&
          x.map((controle) => controle.fr) < 23
        ) {
          item3 = 1;
        } else {
          item3 = 0;
        }
        // obtendo pontuação dos critérios de SIRS:
        var somasepse = item1 + item2 + item3;
        console.log('PONTUAÇÃO SIRS: ' + somasepse);
        // verificando condições:
        if (x.map((controle) => controle.glasgow) < 15) {
          setSepse(1);
        } else if (x.map((controle) => controle.pas) < 100) {
          setSepse(1);
        } else if (x.map((controle) => controle.fr) > 22) {
          setSepse(1);
        } else if (somasepse > 2) {
          setSepse(1);
        } else {
          setSepse(0);
        }
      } else {
        setGlasgow('?');
        setPas('?');
        setPad('?');
        setPam('?');
        setFc('?');
        setFr('?');
        setSao2('?');
        setTax('?');
        setBh('?');
        setDiu('?');
      }
    });
    // calculando o bh acumulado.
    axios.get(html + '/bhacumulado/' + idpct + '/' + type).then((response) => {
      var y = [];
      y = response.data;
      setBha(y.map((item) => item.bhacumulado));
    });
  };

