import React from 'react';
import axios from 'axios';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import swal from 'sweetalert';
import MaskedInput from 'react-maskedinput';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';

function UpdatePaciente() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewUpdatePaciente = useSelector((state) => state.updatePaciente);

  // login.
  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginHospital = useSelector((state) => state.hospital);
  const loginCti = useSelector((state) => state.cti);
  const gestor = useSelector((state) => state.gestor);

  // paciente.
  const idpct = useSelector((state) => state.idpct);
  var box = useSelector((state) => state.box);
  var entrada = useSelector((state) => state.entrada);
  var nome = useSelector((state) => state.nome);
  var dn = useSelector((state) => state.dn);
  var hd = useSelector((state) => state.hd);
  var ap = useSelector((state) => state.ap);
  var alergias = useSelector((state) => state.alergias);
  var medprev = useSelector((state) => state.medprev);
  var exprev = useSelector((state) => state.exprev);
  var hda = useSelector((state) => state.hda);
  var status = useSelector((state) => state.status);
  var inter = useSelector((state) => state.inter);

  // invasões.
  var va = useSelector((state) => state.va);
  var jid = useSelector((state) => state.jid);
  var jie = useSelector((state) => state.jie);
  var subcld = useSelector((state) => state.subcld);
  var subcle = useSelector((state) => state.subcle);
  var radd = useSelector((state) => state.radd);
  var rade = useSelector((state) => state.rade);
  var vfemd = useSelector((state) => state.vfemd);
  var vfeme = useSelector((state) => state.vfeme);
  var afemd = useSelector((state) => state.afemd);
  var afeme = useSelector((state) => state.afeme);
  var svd = useSelector((state) => state.svd);
  var torax = useSelector((state) => state.torax);
  var abd = useSelector((state) => state.abd);
  var snc = useSelector((state) => state.snc);
  var dataVa = useSelector((state) => state.datava);
  var dataJid = useSelector((state) => state.datajid);
  var dataJie = useSelector((state) => state.datajie);
  var dataSubcld = useSelector((state) => state.datasubcld);
  var dataSubcle = useSelector((state) => state.datasubcle);
  var dataRadd = useSelector((state) => state.dataradd);
  var dataRade = useSelector((state) => state.datarade);
  var dataVfemd = useSelector((state) => state.datavfemd);
  var dataVfeme = useSelector((state) => state.datavfeme);
  var dataAfemd = useSelector((state) => state.dataafemd);
  var dataAfeme = useSelector((state) => state.dataafeme);
  var dataSvd = useSelector((state) => state.datasvd);
  var dataTorax = useSelector((state) => state.datatorax);
  var dataAbd = useSelector((state) => state.dataabd);
  var dataSnc = useSelector((state) => state.datasnc);

  // vm.
  var vm = useSelector((state) => state.vm);
  var pp = useSelector((state) => state.pp);
  var peep = useSelector((state) => state.peep);
  var fi = useSelector((state) => state.fi);

  // preenchendo os campos.
  useEffect(() => {
    if (viewUpdatePaciente === 1) {
      document.getElementById('inputBoxUpdate').value = box;
      document.getElementById('inputEntradaUpdate').value = entrada;
      document.getElementById('inputNomeUpdate').value = nome;
      document.getElementById('inputDnUpdate').value = dn;
      document.getElementById('inputHdUpdate').value = hd;
      document.getElementById('inputApUpdate').value = ap;
      document.getElementById('inputAlergiasUpdate').value = alergias;
      document.getElementById('inputMedprevUpdate').value = medprev;
      document.getElementById('inputExprevUpdate').value = exprev;
      document.getElementById('inputHdaUpdate').value = hda;
      if (status === 'ESTÁVEL') {
        document.getElementById('radio1').checked = true;
      } else if (status === 'ALERTA') {
        document.getElementById('radio2').checked = true;
      } else {
        document.getElementById('radio3').checked = true;
      }
    }
  }, [
    viewUpdatePaciente,
    entrada,
    box,
    nome,
    dn,
    hd,
    ap,
    alergias,
    medprev,
    exprev,
    hda,
    status,
  ]);

  // função que atualiza o paciente.
  const sendData = () => {
    var obj = {
      cti: loginCti,
      box: box,
      entrada: entrada,
      nome: nome,
      dn: dn,
      hd: hd,
      ap: ap,
      alergias: alergias,
      medprev: medprev,
      exprev: exprev,
      hda: hda,
      status: status,
      va: va,
      jid: jid,
      jie: jie,
      subcld: subcld,
      subcle: subcle,
      radd: radd,
      rade: rade,
      vfemd: vfemd,
      vfeme: vfeme,
      afemd: afemd,
      afeme: afeme,
      svd: svd,
      torax: torax,
      abd: abd,
      snc: snc,
      datava: dataVa,
      datajid: dataJid,
      datajie: dataJie,
      datasubcld: dataSubcld,
      datasubcle: dataSubcle,
      dataradd: dataRadd,
      datarade: dataRade,
      datavfemd: dataVfemd,
      datavfeme: dataVfeme,
      dataafemd: dataAfemd,
      dataafeme: dataAfeme,
      datasvd: dataSvd,
      datatorax: dataTorax,
      dataabd: dataAbd,
      datasnc: dataSnc,
      vm: vm,
      pp: pp,
      peep: peep,
      fi: fi,
    };
    axios.post(html + '/updatepaciente/' + idpct, obj);
    toastSuccess();
  };

  const close = () => {
    changeState({
      type: 'CORRIDA_ON',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
      payloadGestor: gestor,
      payloadGestor: gestor,
      payloadIdpct: idpct,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadDatava: dataVa,
      payloadJid: jid,
      payloadDatajid: dataJid,
      payloadJie: jie,
      payloadDatajie: dataJie,
      payloadSubcld: subcld,
      payloadDatasubcld: dataSubcld,
      payloadSubcle: subcle,
      payloadDatasubcle: dataSubcle,
      payloadRadd: radd,
      payloadDataradd: dataRadd,
      payloadRade: rade,
      payloadDatarade: dataRade,
      payloadVfemd: vfemd,
      payloadDatavfemd: dataVfemd,
      payloadVfeme: vfeme,
      payloadDatavfeme: dataVfeme,
      payloadAfemd: afemd,
      payloadDataafemd: dataAfemd,
      payloadAfeme: afeme,
      payloadDataafeme: dataAfeme,
      payloadSvd: svd,
      payloadDatasvd: dataSvd,
      payloadSnc: snc,
      payloadDatasnc: dataSnc,
      payloadTorax: torax,
      payloadDatatorax: dataTorax,
      payloadAbd: abd,
      payloadDataabd: dataAbd,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
  };

  const checkFields = () => {
    box = document.getElementById('inputBoxUpdate').value;
    entrada = document.getElementById('inputEntradaUpdate').value;
    nome = document.getElementById('inputNomeUpdate').value.toUpperCase();
    dn = document.getElementById('inputDnUpdate').value;
    hd = document.getElementById('inputHdUpdate').value.toUpperCase();
    ap = document.getElementById('inputApUpdate').value.toUpperCase();
    alergias = document
      .getElementById('inputAlergiasUpdate')
      .value.toUpperCase();
    medprev = document.getElementById('inputMedprevUpdate').value.toUpperCase();
    exprev = document.getElementById('inputExprevUpdate').value.toUpperCase();
    hda = document.getElementById('inputHdaUpdate').value.toUpperCase();
    if (box.length < 2) {
      box = '0' + box;
    }
    if (
      box === '' ||
      entrada === '' ||
      nome === '' ||
      dn === '' ||
      hd === '' ||
      ap === '' ||
      alergias === '' ||
      medprev === '' ||
      exprev === '' ||
      hda === '' ||
      status === ''
    ) {
      swal({
        title: 'ERRO',
        text: 'Por favor, preencha o(s) campo(s) em branco.',
        icon: 'warning',
        dangerMode: true,
      });
    } else {
      sendData();
    }
  };

  // TOAST.
  var toastColor = '';
  var toastMessage = '';
  const toastSuccess = () => {
    toastColor = '#52BE80';
    toastMessage = 'PACIENTE ATUALIZADO COM SUCESSO';
    changeState({
      type: 'TOAST_SUCCESS',
      payloadToastColor: toastColor,
      payloadToastMessage: toastMessage,
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
      payloadGestor: gestor,
      payloadGestor: gestor,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadDatava: dataVa,
      payloadJid: jid,
      payloadDatajid: dataJid,
      payloadJie: jie,
      payloadDatajie: dataJie,
      payloadSubcld: subcld,
      payloadDatasubcld: dataSubcld,
      payloadSubcle: subcle,
      payloadDatasubcle: dataSubcle,
      payloadRadd: radd,
      payloadDataradd: dataRadd,
      payloadRade: rade,
      payloadDatarade: dataRade,
      payloadVfemd: vfemd,
      payloadDatavfemd: dataVfemd,
      payloadVfeme: vfeme,
      payloadDatavfeme: dataVfeme,
      payloadAfemd: afemd,
      payloadDataafemd: dataAfemd,
      payloadAfeme: afeme,
      payloadDataafeme: dataAfeme,
      payloadSvd: svd,
      payloadDatasvd: dataSvd,
      payloadSnc: snc,
      payloadDatasnc: dataSnc,
      payloadTorax: torax,
      payloadDatatorax: dataTorax,
      payloadAbd: abd,
      payloadDataabd: dataAbd,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
    setTimeout(() => {
      changeState({
        type: 'CORRIDA_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
        payloadIdpct: idpct,
        payloadBox: box,
        payloadEntrada: entrada,
        payloadNome: nome,
        payloadDn: dn,
        payloadHd: hd,
        payloadAp: ap,
        payloadAlergias: alergias,
        payloadMedprev: medprev,
        payloadExprev: exprev,
        payloadHda: hda,
        payloadStatus: status,
        payloadVa: va,
        payloadDatava: dataVa,
        payloadJid: jid,
        payloadDatajid: dataJid,
        payloadJie: jie,
        payloadDatajie: dataJie,
        payloadSubcld: subcld,
        payloadDatasubcld: dataSubcld,
        payloadSubcle: subcle,
        payloadDatasubcle: dataSubcle,
        payloadRadd: radd,
        payloadDataradd: dataRadd,
        payloadRade: rade,
        payloadDatarade: dataRade,
        payloadVfemd: vfemd,
        payloadDatavfemd: dataVfemd,
        payloadVfeme: vfeme,
        payloadDatavfeme: dataVfeme,
        payloadAfemd: afemd,
        payloadDataafemd: dataAfemd,
        payloadAfeme: afeme,
        payloadDataafeme: dataAfeme,
        payloadSvd: svd,
        payloadDatasvd: dataSvd,
        payloadSnc: snc,
        payloadDatasnc: dataSnc,
        payloadTorax: torax,
        payloadDatatorax: dataTorax,
        payloadAbd: abd,
        payloadDataabd: dataAbd,
        payloadVm: vm,
        payloadPp: pp,
        payloadPeep: peep,
        payloadFi: fi,
        payloadInter: inter,
      });
    }, 3500);
  };

  // varíavel 'status'.
  const clickStatus0 = () => {
    status = 'INSTÁVEL';
  };
  const clickStatus1 = () => {
    status = 'ALERTA';
  };
  const clickStatus2 = () => {
    status = 'ESTÁVEL';
  };

  // impedindo números grandes para o box.
  const fixBox = () => {
    box = document.getElementById('inputBoxUpdate').value;
    if (box.length > 3) {
      document.getElementById('inputBoxUpdate').value = '';
    }
  };

  // impedindo o preenchimento incompleto de datas.
  const fixEntrada = (e) => {
    entrada = document.getElementById('inputEntradaUpdate').value;
    if (entrada.includes('_')) {
      document.getElementById('inputEntradaUpdate').value = '';
      e.target.placeholder = 'ADM';
    }
  };
  const fixDn = (e) => {
    dn = document.getElementById('inputDnUpdate').value;
    if (dn.includes('_')) {
      document.getElementById('inputDnUpdate').value = '';
      e.target.placeholder = 'DN';
    }
  };

  // renderização do componente.
  if (viewUpdatePaciente === 1) {
    return (
      <div
        className="secondary fade=in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          height: '100%',
          width: '100%',
          marginTop: 0,
          marginBottom: 0,
          marginLeft: 0,
          marginRight: 0,
        }}
      >
        <div>
          <div
            className="secondary"
            style={{
              backgroundColor: '#FFFFFF',
              borderRadius: 5,
              paddingTop: 30,
              paddingBottom: 30,
              paddingLeft: 30,
              paddingRight: 30,
            }}
          >
            <label
              className="title2"
              style={{ marginTop: 0, marginBottom: 15 }}
            >
              ATUALIZAR PACIENTE
            </label>
            <div
              className="scrollview"
              style={{
                justifyContent: 'flex-start',
                backgroundColor: '#F1F3F9',
                borderColor: '#F1F3F9',
                height: 350,
                marginTop: 0,
                marginBottom: 0,
              }}
            >
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  marginBottom: 0,
                }}
              >
                <label>
                  <label
                    className="title2"
                    style={{ marginTop: 15, fontSize: 14 }}
                  >
                    BOX:
                  </label>
                  <div>
                    <input
                      id="inputBoxUpdate"
                      className="input"
                      title="BOX."
                      type="number"
                      onChange={() => fixBox()}
                      maxLength="3"
                      placeholder="BOX"
                      autoComplete="off"
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) => (e.target.placeholder = 'BOX')}
                      style={{
                        marginBottom: 0,
                        marginRight: 0,
                        width: 50,
                        height: 50,
                      }}
                    ></input>
                  </div>
                </label>
                <div>
                  <label
                    className="title2"
                    style={{ marginTop: 15, fontSize: 14, marginBottom: 0 }}
                  >
                    ADM:
                  </label>
                  <label>
                    <MaskedInput
                      className="input"
                      title="DATA DE ADMISSÃO."
                      placeholder="ADM"
                      autoComplete="off"
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) => fixEntrada(e)}
                      style={{
                        marginBottom: 0,
                        marginRight: 0,
                        width: 100,
                        height: 50,
                      }}
                      mask="11/11/1111"
                      id="inputEntradaUpdate"
                    />
                  </label>
                </div>
                <div>
                  <label
                    className="title2"
                    style={{ marginTop: 15, fontSize: 14 }}
                  >
                    NOME:
                  </label>
                  <input
                    className="input"
                    title="NOME."
                    placeholder="NOME"
                    autoComplete="off"
                    maxLength="50"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) => (e.target.placeholder = 'NOME')}
                    style={{
                      marginBottom: 0,
                      width: 440,
                      height: 50,
                    }}
                    type="text"
                    id="inputNomeUpdate"
                  ></input>
                </div>
                <div>
                  <label
                    className="title2"
                    style={{ marginTop: 15, fontSize: 14, marginBottom: 0 }}
                  >
                    DN:
                  </label>
                  <label>
                    <MaskedInput
                      title="DATA DE NASCIMENTO."
                      autoComplete="off"
                      placeholder="DN"
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) => fixDn(e)}
                      className="input"
                      style={{
                        marginBottom: 0,
                        marginLeft: 0,
                        width: 100,
                        height: 50,
                      }}
                      mask="11/11/1111"
                      id="inputDnUpdate"
                    />
                  </label>
                </div>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  marginTop: 5,
                  marginBottom: 0,
                }}
              >
                <div
                  class="radio-toolbar"
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    marginTop: 0,
                    marginBottom: 0,
                  }}
                >
                  <input
                    type="radio"
                    id="radio1"
                    name="status"
                    value="ESTÁVEL"
                    onClick={() => clickStatus2()}
                  ></input>
                  <label for="radio1">ESTÁVEL</label>
                  <input
                    type="radio"
                    id="radio2"
                    name="status"
                    value="ALERTA"
                    onClick={() => clickStatus1()}
                  ></input>
                  <label for="radio2">ALERTA</label>
                  <input
                    type="radio"
                    id="radio3"
                    name="status"
                    value="INSTÁVEL"
                    onClick={() => clickStatus0()}
                  ></input>
                  <label for="radio3">INSTÁVEL</label>
                </div>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  marginTop: 0,
                }}
              >
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    ANTECEDENTES PESSOAIS:
                  </label>
                  <textarea
                    className="textarea"
                    title="ANTECEDENTES PESSOAIS."
                    id="inputApUpdate"
                    type="text"
                    maxLength="200"
                    autoComplete="off"
                    placeholder="ANTECEDENTES PESSOAIS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) =>
                      (e.target.placeholder = 'ANTECEDENTES PESSOAIS')
                    }
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      marginRight: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    ALERGIAS:
                  </label>
                  <textarea
                    className="textarea"
                    title="ALERGIAS."
                    id="inputAlergiasUpdate"
                    type="text"
                    maxLength="200"
                    autoComplete="off"
                    placeholder="ALERGIAS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) => (e.target.placeholder = 'ALERGIAS')}
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                }}
              >
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    MEDICAÇÕES PRÉVIAS:
                  </label>
                  <textarea
                    className="textarea"
                    title="MEDICAÇÕES PRÉVIAS."
                    id="inputMedprevUpdate"
                    type="text"
                    maxLength="200"
                    autoComplete="off"
                    placeholder="MEDICAÇÕES PRÉVIAS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) =>
                      (e.target.placeholder = 'MEDICAÇÕES PRÉVIAS')
                    }
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      marginRight: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    EXAMES PRÉVIOS:
                  </label>
                  <textarea
                    className="textarea"
                    title="EXAMES PRÉVIOS."
                    id="inputExprevUpdate"
                    type="text"
                    maxLength="200"
                    autoComplete="off"
                    placeholder="EXAMES PRÉVIOS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) => (e.target.placeholder = 'EXAMES PRÉVIOS')}
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                }}
              >
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    HIPÓTESES DIAGNÓSTICAS:
                  </label>
                  <textarea
                    className="textarea"
                    title="HIPÓTESES DIAGNÓSTICAS."
                    id="inputHdUpdate"
                    type="text"
                    maxLength="200"
                    autoComplete="off"
                    placeholder="HIPÓTESES DIAGNÓSTICAS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) =>
                      (e.target.placeholder = 'HIPÓTESES DIAGNÓSTICAS')
                    }
                    style={{
                      marginBottom: 0,
                      marginRight: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    HISTÓRIA DA DOENÇA ATUAL:
                  </label>
                  <textarea
                    className="textarea"
                    title="HISTÓRIA DA DOENÇA ATUAL."
                    id="inputHdaUpdate"
                    type="text"
                    maxLength="200"
                    autoComplete="off"
                    placeholder="HISTÓRIA DA DOENÇA ATUAL"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) =>
                      (e.target.placeholder = 'HISTÓRIA DA DOENÇA ATUAL')
                    }
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
              }}
            >
              <button
                className="red-button"
                onClick={() => close()}
                style={{
                  marginTop: 35,
                }}
              >
                <img
                  alt=""
                  src={deletar}
                  style={{
                    marginTop: 20,
                    marginBottom: 20,
                    marginLeft: 20,
                    marginRight: 20,
                    height: 20,
                    width: 20,
                  }}
                ></img>
              </button>
              <button
                className="green-button"
                onClick={() => checkFields()}
                style={{
                  marginTop: 35,
                  marginRight: 0,
                }}
              >
                <img
                  alt=""
                  src={salvar}
                  style={{
                    marginTop: 20,
                    marginBottom: 20,
                    marginLeft: 20,
                    marginRight: 20,
                    height: 20,
                    width: 20,
                  }}
                ></img>
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default UpdatePaciente;
