import axios from 'axios';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/pulsar.svg';
import '../design.css';

function Login() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewLogin = useSelector((state) => state.login);

  var login = null;
  var senha = null;

  const [listhospital, setListhospital] = useState([]);
  const [listuser, setListuser] = useState([]);

  useEffect(() => {
    if (viewLogin === 1) {
      setListhospital([]);
      setSelectHospital('HOSPITAL');
      setDisablebutton(true);
      document.getElementById('inputLogin').value = '';
      document.getElementById('inputSenha').value = '';
    }
  }, [viewLogin]);

  // renderização da lista de hospitais.
  const [viewhospitallist, setViewhospitallist] = useState('');
  const [selecthospital, setSelectHospital] = useState('HOSPITAL');
  function ShowHospitais() {
    if (viewhospitallist === 1) {
      return (
        <div
          className="secondary fade-in"
          style={{
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            height: '100%',
            width: '100%',
            marginTop: 0,
            marginBottom: 0,
            marginLeft: 0,
            marginRight: 0,
            zIndex: 3,
          }}
        >
          <div
            className="secondary"
            style={{
              backgroundColor: '#FFFFFF',
              borderRadius: 5,
              paddingTop: 30,
              paddingBottom: 30,
              paddingLeft: 30,
              paddingRight: 30,
            }}
          >
            <label
              className="title2"
              style={{ marginTop: 0, marginBottom: 15 }}
            >
              SELECIONAR HOSPITAL
            </label>
            <div
              className="scrollpacientes"
              id="LISTA DE HOSPITAIS"
              style={{
                justifyContent: 'flex-start',
                borderRadius: 5,
                marginRight: 5,
              }}
            >
              {listhospital.map((item) => (
                <p
                  key={item.id}
                  className="row"
                  style={{
                    position: 'relative',
                    marginLeft: 0,
                    marginRight: 0,
                    marginTop: 0,
                    marginBottom: 0,
                  }}
                >
                  <button
                    className="widget"
                    style={{ width: 300, marginRight: 5, color: '#ffffff' }}
                    onClick={() => clickHospital(item.hospital)}
                  >
                    {item.hospital}
                  </button>
                </p>
              ))}
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // toast.
  const toastFail = () => {
    var toastColor = '#EC7063';
    var toastMessage = 'CREDENCIAIS INCORRETAS.';
    changeState({
      type: 'TOAST_LOGIN_ON',
      payloadToastColor: toastColor,
      payloadToastMessage: toastMessage,
    });
    setTimeout(() => {
      changeState({
        type: 'TOAST_LOGIN_OFF',
      });
    }, 2500);
  };

  // função que retorna o valor de login ao digitarmos no campo login e busca os hospitais.
  function WelcomeGestor() {
    if (welcomeboss === 1) {
      return (
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <div
            className="title2 fade-in"
            style={{
              fontSize: 14,
              height: 50,
              padding: 10,
              marginTop: 10,
              marginBottom: 5,
            }}
          >
            {'BEM-VINDO, CHEFE!'}{' '}
          </div>
          <div>
            <button
              onClick={() => checkLogin()}
              className="blue-button"
              style={{ width: 100 }}
            >
              OK
            </button>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  const [disablebutton, setDisablebutton] = useState(true);
  const [hidebutton, setHidebutton] = useState('flex');
  const [welcomeboss, setWelcomeboss] = useState(60);
  const setLogin = () => {
    setDisablebutton(true);
    login = document.getElementById('inputLogin').value;
    console.log(login);
    // rota para identificação do tipo de usuário (plantonista = 0, gestor = 1).
    axios
      .get(html + "/login/'" + login.toUpperCase() + "'")
      .then((response) => {
        setListuser(response.data);
        var x = response.data;
        const arrayGestor = x.map((item) => item.gestor);
        const loginGestor = arrayGestor[0];
        console.log('gestor: ' + loginGestor);
        if (x.length > 0 && loginGestor === 0) {
          setHidebutton('flex');
          setWelcomeboss(0);
          getHospitaisList();
        } else if (x.length > 0 && loginGestor === 1) {
          setHidebutton('none');
          setWelcomeboss(1);
        } else {
          setListhospital([]);
          setHidebutton('flex');
          setDisablebutton(true);
          setWelcomeboss(0);
        }
      });
  };

  // rota para seleção do hospital para usuário plantonista.
  const getHospitaisList = () => {
    axios.get(html + "/hospitais/'" + login + "'").then((response) => {
      setListhospital(response.data);
      var x = response.data;
      if (x.length > 0) {
        setDisablebutton(false);
      } else {
        setDisablebutton(true);
      }
    });
  };

  // função que busca um registro de login válido no servidor.
  const checkLogin = () => {
    login = document.getElementById('inputLogin').value;
    senha = document.getElementById('inputSenha').value;
    var obj = {
      user: login,
      password: senha.toUpperCase(),
    };
    axios
      .post(html + "/login/'" + login + "'/'" + senha.toUpperCase() + "'", obj)
      .then((res) => {
        var x = []; // se o tipo da variável x não for definido como array [], a função .map abaixo não funciona.
        x = res.data;
        const arrayUsuario = x.map((login) => login.nome);
        const loginUsuario = arrayUsuario[0];
        const arrayGestor = x.map((login) => login.gestor);
        const loginGestor = arrayGestor[0];
        setTimeout(function () {
          // eslint-disable-next-line
          if (x.length > 0 && loginGestor === 0) {
            console.log(
              'LOGIN EFETUADO COM SUCESSO: USUÁRIO ' + loginUsuario + '.'
            );
            changeState({
              type: 'CTI_ON',
              payloadUserLogin: login,
              payloadUserName: loginUsuario,
              payloadHospital: nosocomio,
              payloadGestor: loginGestor,
            });
          } else if (x.length > 0 && loginGestor === 1) {
            console.log(
              'LOGIN EFETUADO COM SUCESSO: GESTOR ' + loginUsuario + '.'
            );
            changeState({
              type: 'GESTOR_ON',
              payloadUserLogin: login,
              payloadUserName: loginUsuario,
              payloadGestor: loginGestor,
              payloadHospital: nosocomio,
            });
          } else {
            console.log('GESTOR: ' + loginGestor);
            toastFail();
            setTimeout(() => {
              changeState({ type: 'LOGIN_OFF' });
              changeState({ type: 'LOGIN_ON' });
            }, 2000);
          }
        }, 1000);
      });
  };

  var nosocomio = '';
  const clickHospital = (value) => {
    setSelectHospital(value);
    setViewhospitallist(0);
    nosocomio = value;
    checkLogin();
  };

  // renderização do componente.
  if (viewLogin === 1) {
    return (
      <div className="main fade-in">
        <img alt="" src={logo} style={{ height: 200 }}></img>
        <p className="title1" style={{ marginBottom: 0 }}>
          GPULSE WEB
        </p>
        <p className="title2" style={{ fontSize: 20 }}>
          SEU PASSÔMETRO DIGITAL!
        </p>
        <ShowHospitais></ShowHospitais>
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <div>
            <input
              autoComplete="off"
              placeholder="LOGIN"
              className="input"
              type="text"
              id="inputLogin"
              onChange={() => setLogin()}
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'LOGIN')}
              style={{
                marginTop: 20,
                marginBottom: 10,
                width: 200,
                height: 50,
              }}
            ></input>
          </div>
          <div>
            <input
              autoComplete="off"
              placeholder="SENHA"
              className="input"
              type="password"
              id="inputSenha"
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'SENHA')}
              style={{
                marginTop: 0,
                marginBottom: 0,
                width: 200,
                height: 50,
              }}
            ></input>
          </div>
          <button
            onClick={() => setViewhospitallist(1)}
            title={selecthospital}
            disabled={disablebutton}
            className="widget fade-in"
            style={{
              display: hidebutton,
              width: 200,
              padding: 10,
              marginTop: 10,
              marginBottom: 20,
              backgroundColor:
                listhospital.length < 1
                  ? 'rgba(32, 122, 140, 0.3)'
                  : 'rgba(32, 122, 140, 0.8)',
            }}
          >
            {selecthospital}
          </button>
          <WelcomeGestor></WelcomeGestor>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Login;
